import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ClaimListComponent } from './claim-list.component';
import { VersionControlResolver } from 'src/app/resolvers/versionControl.resolver';
import { Route, RouterModule } from '@angular/router';
import { DxTagBoxModule } from 'devextreme-angular';

const claimListRoutes: Route[] = [
  {
    path: '',
    component: ClaimListComponent,
    resolve: { version: VersionControlResolver }
  },
];

@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forChild(claimListRoutes),
    DxTagBoxModule
  ]
})
export class ClaimListModule { }
